<template>
    <div class="main-page">
        <div class="headerTag">选择车辆</div>
        <van-tabs v-model="active" @change="change" v-if="classList.length" title-active-color="#409EFF" line-width="60px" color="#409EFF">
            <van-tab :title="item.ClassName" v-for="(item, index) in classList" :key="index" :name="item.Id" title-style="font-size:14px;font-weight:600;">
            </van-tab>
            <ScrollView ref="ScrollView" top="88px" bottom="0" @down="down" @upFun="up">
                <div class="persons">
                    <div class="persons-line" v-for="(item,index) in carsList" :key="index" @click="chooseItem(item)">
                        <p class="name">{{item.FCarNo}}</p>&nbsp;
                        <p class="orgname"> [ {{item.FName}} ]</p>
                        <p class="orgname"> [ {{FType[item.FType]}} ]</p>
                    </div>
                </div>
            </ScrollView>
        </van-tabs>
    </div>
</template>

<script>
export default {
    name: "CarsChoose",
    props: {},
    components: {},

    data() {
        return {
            carsList: [],
            FType: {
                101: "终端车",
                301: "中转车"
            },
            classList: [{ ClassName: "终端车", FType: 101 }, { ClassName: "中转车", FType: 301 }],
            active: 0,
            searchInfo: {
                FType: 101
            }
        };
    },
    methods: {
        VMS_CarIndex() {
            this._api.VMS_CarIndex().then(res => {
                if (res) {
                    this.carsList = res.data
                }
            })
        },
        chooseItem(item) {
            // this.$u.confirm(`请确认选择 [ ${item.FCarNo} ]`).then(() => {
            window.temFun(item)
            this.$router.back()
            // }).catch(() => { })
        },
        down(instance) {
            this.$refs["ScrollView"].reUp();
        },
        // 上拉加载
        up([page, instance]) {
            this._api
                .VMS_CarIndex({
                    ...this.searchInfo,
                    page: page.num,
                    pageSize: page.size
                })
                .then((res) => {
                    if (res) {
                        let arr = res.data;
                        if (page.num === 1) this.dataList = [];
                        this.carsList = this.dataList.concat(arr);
                        this.$nextTick(() => {
                            instance.endSuccess(arr.length);
                        });
                    }
                    this.loading = false;
                });
        },
        change(val) {
            this.searchInfo.FType = val ? 301 : 101
            this.$refs.ScrollView.reUp()
        }
    },
    mounted() { }

};
</script>

<style lang='scss' scoped>
.orgs {
    width: 100%;
    height: 44px;
    border-bottom: 1px solid #f5f6f7;
    border-top: 1px solid #f5f6f7;
    padding: 0 15px;
}

.persons {
    width: 100%;
    height: calc(100% - 84px);
    padding: 0 15px;

    .persons-line {
        display: flex;
        align-items: center;
        width: 100%;
        height: 44px;
        border-bottom: 1px solid #f5f6f7;
        white-space: nowrap;
        overflow: auto;

        .orgname {
            font-size: 14px;
        }
    }
}
</style>
